import React, { useEffect } from 'react';
import { useAppState } from '../../shared/state';
import config from '../../config';
import { CaseType } from '../../shared/state/reducer';
import isEmpty from 'lodash/isEmpty';
import { useRouter } from '../../shared/hooks';

function InitialLoaderPage() {
	const { hasUnread, setCaseType, groupsUnreadCountData } = useAppState();
	const { history } = useRouter();

	//handle redirecting to appropriate pages according to unread count
	useEffect(() => {
		//If there is no unread redirect to Dashboard page
		if (!hasUnread.staff && !hasUnread.client && isEmpty(groupsUnreadCountData)) {
			history.push('/dashboard');
			return;
		}

		if (hasUnread.staff) {
			setCaseType(config.STAFF as CaseType);
			history.push('/case-messages');
			return;
		}

		if (hasUnread.client) {
			setCaseType(config.CLIENT as CaseType);
			history.push('/case-messages');
			return;
		}

		if (!isEmpty(groupsUnreadCountData)) {
			history.push('/group-chat');
			return;
		}
	}, []);

	return (
		<div className={'flex h-screen w-full items-center justify-center'}>
			Counting unread messages... it will take a little time...
		</div>
	);
}

export default InitialLoaderPage;
